// Default Style Reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  outline: none;
  font-size: 14px;
}

// Variables
$color-primary: #1f6f8b;
$color-dark: #1c2b2d;
$color-light: #ffffff;
$color-bg: #eeeeee;

// Dark Mode Variants
// $color-light: #383838;
// $color-dark: #ffffff;
// $color-bg: #121212;

@font-face {
  font-family: 'Roboto';
  src: url('./Roboto-Regular.ttf');
}

// General Styles
body {
  font-family: 'Roboto', sans-serif;
  background-color: $color-bg;

  ul {
    padding: 0;
    list-style: none;
  }

  p {
    margin: 5px 0;
  }

  h1 {
    margin-bottom: 0.25em;
    color: $color-primary;
    letter-spacing: 1px;
    font-size: 2.5em;
  }
  h2,
  label {
    opacity: 0.9;
    font-weight: bold;
    color: $color-primary;
    font-weight: normal;
    font-size: 1.1em;
  }

  input {
    text-align: center;
    padding: 8px 6px 8px 10px;
    border-radius: 5px;
    border: 2px solid $color-light;
    outline: none;
    background-color: #ffffff;
    color: $color-dark;
    font-size: 14px;
    font-weight: bold;
    line-height: 20px;
    align-self: center;
    height: 38px;
  }
}

// App Component
.App {
  padding: 2% 5%;
  margin-top: 1em;
  display: flex;
  flex-direction: column;
  text-align: center;
}

// Home Component
.Home {
  margin: 1em;
  text-align: center;
  font-size: 10px;

  h1 {
    margin-bottom: 1em;
  }
  h2 {
    font-size: 1.5em;
    font-weight: bold;
    margin-top: 1em;
  }

  .About {
    margin: 1em auto;

    section {
      margin: 1em auto;
    }
    h3 {
      font-size: 1.25em;
      color: $color-primary;
      font-weight: normal;
    }
    p {
      margin: 0.5em auto;
      max-width: 600px;
      font-weight: normal;
    }
  }

  // Login Component (Google Verification)
  .login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    padding: 20px;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
  }

  .google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  }

  .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
  }

  .google-btn:active {
    background: #1669f2;
  }

  .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
  }

  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
  }

  .btn-text {
    float: right;
    margin: 11px 11px 0 0;
    color: #fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: 'Roboto';
  }

  .privacy {
    text-decoration: none;
    letter-spacing: 1px;
    margin: 1em;
    color: #f27516;
  }
}

// CitySearch Component
.CitySearch {
  display: flex;
  flex-direction: column;
  max-width: 300px;
  margin: auto;
  position: relative;

  .city {
    width: 250px;
  }

  .show-suggestions {
    display: block;
  }

  .display-none {
    display: none;
  }

  .suggestions {
    border-radius: 4px;
    width: 250px;
    position: absolute;
    z-index: 2;
    align-self: center;
    margin: 0;
    top: 54px;
    font-size: 14px;
  }

  .suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #dddddd;
    cursor: default;
  }

  .suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  .suggestions > li:hover {
    background-color: $color-primary;
    color: $color-light;
  }
}

// NumberOfEvents Component
.NumberOfEvents {
  width: 300px;
  margin: 1em auto;
  margin-top: 0.5em;
  margin-bottom: 0;
  text-align: center;
}

// DataVisualization Component
.data-vis-wrapper {
  position: relative;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr;
  gap: 0.5em;
  margin-bottom: 1em;

  h4 {
    font-size: 16px;
    color: $color-primary;
    text-align: center;
  }

  .data-pie-wrapper,
  .data-scatter-wrapper {
    position: relative;
    height: 450px;
    border-radius: 5px;
  }

  .data-scatter-wrapper {
    display: none;

    .recharts-wrapper tspan {
      font-size: 11px;
    }
  }

  .data-pie-wrapper {
    height: 270px;
    width: 380px;
    justify-self: center;

    .recharts-wrapper tspan {
      font-size: 12px;
    }
  }

  .data-scatter-plot,
  .data-pie-chart {
    box-shadow: 3px 2px 2px 2px rgba(0, 0, 0, 0.075);
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    background-color: #fff;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

// EventList Component
.EventList {
  width: 100%;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 700px));
  gap: 0.5em;
}

// Event Component
.Event {
  background-color: $color-light;
  color: $color-dark;
  padding: 1em;
  margin-bottom: 0.5em;
  text-align: left;
  border-radius: 5px;
  position: relative;
  box-shadow: 3px 2px 2px 2px rgba(0, 0, 0, 0.075);
  opacity: 0.92;

  .summary {
    opacity: 1;
    font-size: 1.5em;
    font-weight: bold;
    letter-spacing: 0.5px;
    word-spacing: 2px;
    color: $color-primary;
    margin-bottom: 0.5em;
  }

  .subheading {
    font-size: 1em;
    margin-bottom: 3em;
  }

  .time {
    font-weight: bold;
  }

  .description {
    margin-bottom: 3em;
  }

  .btn-details {
    font-family: 'Roboto', sans-serif;
    border: none;
    background-color: $color-primary;
    color: #ffffff;
    width: 100px;
    font-size: 1em;
    position: absolute;
    right: 1em;
    bottom: 1em;
    padding: 0.6em 0.3em;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
  }

  .Expanded-Event {
    h4 {
      margin: 1em 0;
      color: $color-primary;
    }

    .link-container {
      margin-bottom: 1em;
    }

    a.link {
      color: $color-primary;
    }
  }
}

// Alert Component
.Alert {
  font-weight: bold;
  font-style: italic;
  margin-top: 0.5em;
}

@media all and (min-width: 1576px) {
  .data-vis-wrapper {
    grid-template-columns: 1fr;

    .data-scatter-wrapper {
      display: unset;
    }
  }
}
